* {
  font-family: Helvetica, Arial, sans-serif;
}
body {
  padding: 60px;
}

input {
  width: 100%;
  height: 20px;
  line-height: 20px;
  color: grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img{
  width: 100%;
  height: 100%;
}